.img {
  width: 200%;
  height: 420px;
  display: inline-block;
  margin-bottom: -5%;
  margin-left: -2%;
}

.edit {
  margin-bottom: 5%;
}

.edit > input {
  border-radius: 8px;
  border: 2px solid #dddfe2;
  outline: none;
  color: #1d2129;
  margin: 0.5rem 0;
  padding: 0.5rem 0.75rem;
  width: 92%;
  font-size: 1rem;
}

.edit > .button {
  background: #013221;
  border: 1px solid #025538;
  color: #fff;
  font-size: 1.25rem;
  padding: 0.5rem;
  margin: 0.5rem 0;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
}

.edit input {
  margin-top: 10px;
  padding: 10px;
  border: none;
  border-radius: 20px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  font-size: 16px;
}

.edit button {
  width: 70%;
  margin-top: 20px;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  background-color: #1c0040;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

.edit button:hover {
  background-color: #7136bd;
}

.error {
  color: red;
  font-size: 14px;
  margin-top: 5px;
  text-align: left;
}

.image {
  margin-left: 30px;
}
.up {
  display: flex;
}
.pl {
  background-color: rgb(219, 242, 242);
}
